<template>
  <a-spin :spinning="spinning" size="large">
    <div class="user">
      <!-- 导航栏 -->
      <Header :style="headerStyle"></Header>
      <!-- 横幅 -->
      <div class="banner container animated fadeIn">
        <!-- 功能栏，包括跳到用户主页以及更换壁纸 -->
        <div class="functionBar">
          <a-space size="large">
            <a-icon type="eye" style="font-size: 24px" @click="toUserIndex" />
            <a-icon type="menu" style="font-size: 24px" @click="showDrawer" />
          </a-space>
        </div>
        <!-- 用户模块 -->
        <div class="userInfo">
          <a-row type="flex">
            <!-- 头像 -->
            <a-col>
              <a-avatar src="images/avatar1.png" :size="80" alt="Error"></a-avatar>
            </a-col>
            <!-- 用户信息 -->
            <a-col>
              <div class="info">
                <div class="NickName">
                  <a-row type="flex" align="bottom">
                    <a-space>
                      <a-col><div>上班睡觉下班通宵</div></a-col>
                      <a-col><img style="width: 24px; height: 24px" src="../../public/images/boy.png" alt="" /></a-col>
                      <a-col>
                        <a-tag color="#87d068">Lv999</a-tag>
                      </a-col>
                    </a-space>
                  </a-row>
                </div>
                <div>
                  <template v-if="isEdit">
                    <!-- 个性签名输入框 -->
                    <a-input ref="personalSignature" v-model="tempPersonalSignature" style="width: 720px" @pressEnter="changePersonalSignature" @blur="changePersonalSignature" />
                  </template>
                  <template v-else>
                    <!-- 个性签名显示 -->
                    <a-row type="flex" justify="center" align="middle">
                      <a-space>
                        <p>{{ personalSignature }}</p>
                        <a-icon type="edit" theme="filled" @click="showInput" />
                      </a-space>
                    </a-row>
                  </template>
                </div>
              </div>
            </a-col>
          </a-row>
        </div>
        <!-- 背景视频 -->
        <div class="video">
          <video :src="bgVideoSrc" muted autoplay loop></video>
        </div>
      </div>
      <!-- 底部抽屉 -->
      <a-drawer title="更改背景" :visible="drawerIsVisible" placement="bottom" :maskClosable="true" @close="hideDrawer" :getContainer="getContainer" :destroyOnClose="true">
        <!-- 背景列表 -->
        <a-row type="flex" justify="space-between" :gutter="[24, 24]">
          <a-col v-for="(item, index) in videoList" :key="index" :span="12">
            <video :src="item" muted autoplay loop @click="changeBgVideo(item)"></video>
          </a-col>
        </a-row>
      </a-drawer>
      <!-- 菜单栏 -->
      <div class="container">
        <a-menu :selectedKeys="selectedKeys" mode="horizontal" @click="jump">
          <a-menu-item key="userIndex">主页</a-menu-item>
          <a-menu-item key="userForum">论坛 0</a-menu-item>
          <a-menu-item key="userCourse">课程 0</a-menu-item>
          <a-menu-item key="userSaySomeThing">说说 0</a-menu-item>
          <a-menu-item key="userAccompanyMe">伴我</a-menu-item>
          <a-menu-item key="userSettings">设置</a-menu-item>
        </a-menu>
      </div>
      <!-- 路由 -->
      <div class="router-view">
        <router-view />
      </div>
      <!-- 底部栏 -->
      <Footer theme="white" style="background: rgb(239, 243, 245)"></Footer>
    </div>
  </a-spin>
</template>

<script>
export default {
  name: "user",
  data() {
    return {
      headerStyle: {
        background: "url(images/b1.jpg)",
        backgroundAttachment: "fixed",
      },
      isEdit: false,
      tempPersonalSignature: "",
      personalSignature: "宝，我今天去输液了，输的什么液，想你的夜！",
      spinning: false,
      drawerIsVisible: false,
      bgVideoSrc: "video/v2.mp4",
      videoList: ["video/v2.mp4", "video/v3.mp4", "video/v4.mp4", "video/v5.mp4"],
    };
  },
  methods: {
    // 显示个性签名输入框
    showInput() {
      this.tempPersonalSignature = this.personalSignature;
      this.isEdit = true;
      this.$nextTick(() => {
        this.$refs.personalSignature.focus();
      });
    },
    // 修改个性签名
    changePersonalSignature() {
      this.spinning = true;
      this.personalSignature = this.tempPersonalSignature;
      window.setTimeout(() => {
        this.isEdit = false;
        this.$message.success({ content: "修改个性签名成功！", key: "user" });
        this.spinning = false;
      }, 2000);
    },
    // 跳转到用户主页
    toUserIndex() {
      this.$message.success({
        content: "跳转到用户主页，但我不是很想跳！",
        key: "user",
      });
    },
    // 显示背景选择抽屉
    showDrawer() {
      this.drawerIsVisible = true;
    },
    // 隐藏背景选择抽屉
    hideDrawer() {
      this.drawerIsVisible = false;
    },
    // 指定 Drawer 挂载的 HTML 节点
    getContainer() {
      return document.querySelector(".user");
    },
    // 更改背景
    changeBgVideo(src) {
      this.bgVideoSrc = src;
      this.drawerIsVisible = false;
    },
    // 跳转
    jump({ key }) {
      this.$router.push({ name: key });
    },
  },
  computed: {
    // 当前选中的菜单项 key 数组
    selectedKeys() {
      let path = this.$route.path;
      switch (path) {
        case "/user/index":
          return ["userIndex"];
        case "/user/forum":
          return ["userForum"];
        case "/user/course":
          return ["userCourse"];
        case "/user/saySomeThing":
          return ["userSaySomeThing"];
        case "/user/accompanyMe":
          return ["userAccompanyMe"];
        case "/user/settings":
          return ["userSettings"];
        default:
          return ["userIndex"];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.user::v-deep {
  background: rgb(239, 243, 245);
  user-select: none;
  padding-bottom: 141px;
  min-height: @min-height;
  box-sizing: border-box;
  position: relative;
  .banner {
    height: 220px;
    overflow: hidden;
    position: relative;
    flex: none;
    .functionBar {
      position: absolute;
      right: 48px;
      top: 12px;
      cursor: pointer;
      z-index: 2;
      padding: 10px;
      border-radius: 23px;
      overflow: hidden;
      background: rgba(0, 0, 0, 0.33);
      .anticon-eye,
      .anticon-menu {
        transition: all 0.2s ease-out;
        color: #fff;
        &:hover {
          color: #108ee9;
        }
      }
    }
    .userInfo {
      position: absolute;
      left: 24px;
      bottom: 24px;
      z-index: 2;
      color: @colorI;
      .info {
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: space-between;
        padding: 6px;
        .NickName {
          font-size: @fontG;
        }
        p {
          max-width: 900px;
          margin: 0;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        > div:last-child {
          font-size: @fontI;
          display: flex;
          align-items: center;
          .anticon {
            cursor: pointer;
          }
        }
      }
    }
    .video {
      width: 100%;
      height: 100%;
      overflow: hidden;
      video {
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
      }
    }
  }
  .ant-drawer-content-wrapper {
    height: auto !important;
    .ant-drawer-body {
      width: @min-width;
      margin: 0 auto;
      .ant-col {
        height: 120px;
        overflow: hidden;
        cursor: pointer;
      }
    }
  }
  .router-view {
    margin: 12px 0;
    display: flex;
    overflow: auto;
  }
}
</style>
